<template>
<div class="wraper_box">
  <Wraper
    class="wraper"
    v-for="(item, index) in configs"
    :key="index"
    :label="item.label"
    :url="item.url"
    :id="item.id"
    :current="current"
    @startHandle="startHandle"
  />
</div>
</template>
<script>
import { ref } from 'vue'
import Wraper from "@/components/wraper.vue"

export default {
  components: {
    Wraper
  },
  setup() {
    const current = ref()

    const configs = [
      { url: require('@/assets/1.mp3'), label: '行进步和行礼', id: '1' },
      { url: require('@/assets/23.mp3'), label: '小士兵', id: '23' },
    ]

    const startHandle = id => {
      current.value = id
    }

    return {
      configs,
      current,
      startHandle
    }
  },
}
</script>

<style>
.wraper_box {
  overflow: hidden;
}
.wraper {
  width: 50%;
  float: left;
}
</style>
