<template>
  <div class="wraper" @click="startHandle">
    <i class="icon iconfont icon-iconfontplay2"></i>
    <div class="label">{{ label }}</div>
    <audio ref="audio">
      <source :src="url" >
    </audio>
  </div>
</template>
<script>
import { ref, watch } from 'vue'

export default {
  props: {
    label: String,
    url: String,
    id: String,
    current: String
  },
  setup(props, { emit }) {
    const audio = ref(false)

    const startHandle = () => {
      emit('startHandle', props.id)
      audio.value.play()
    }

    const pauseHanlde = () => {
      audio.value.pause()
      audio.value.load()
    }

    watch(
      () => props.current,
      val => {
        if (val === props.id) {
          startHandle()
        } else {
          pauseHanlde()
        }
      }
    )

    return {
      audio,
      startHandle
    }
  },
}
</script>
<style scoped>
.wraper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 20px;
}
.icon {
  margin-bottom: 20px;
}
.label {
  font-size: 20px;
  color: #333;
}
</style>
